.UnderDevelopmentContainer{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;

    .Text{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 100;

        color: white;
        text-shadow: 0 0 40px black;
        text-align: center;

        h{
            font-size: 50px;
            font-weight: bold;
        }

        p{
            position: relative;
            font-size: 40px;
        }

        a{
            text-decoration: none;
            color:bisque;
            font-size: 30px;
        }
        
    }

    .Cheese{
        width: 100%;
        height: 100%;
        background-color: black;
        position: absolute;
        z-index: 90;
        opacity: 20%;
    }

    .Image{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 80;
        transform: translate(0,10);
        align-items: center;
        object-fit: cover;
    }
}